import Authorization from "../user/auth.service";

const CountryService = {
    getPolesByCountry(countryid) {
        try {
            return Authorization.checkLogin(`/poles/by-country/${countryid}`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            name: item.alias,
                            active: false
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },    
    
}

export default CountryService